body {
    background-color: #f1f1f1;
    padding: 20px;
    margin: 20;
  }
  h1, h2, p, ul, li {
    font-family: monospace;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  ul.header {
    background-color: #111;
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #FFF;
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }

  .container-div{ 
    Display: flex;
    Height: 100%;
    Width: 100%; 
    align-content: center;
    justify-content: center;
    }

